import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import vuetify from "./plugins/vuetify";
import store from "./store";
import "@babel/polyfill";
import * as fb from "firebase/app";

Vue.config.productionTip = false;

new Vue({
  router,
  vuetify,
  store,
  created() {
    let firebaseConfig = {
      apiKey: "AIzaSyBOlxcj6q4cVSDLS-zrdXjXGkuM2PTYgXU",
      authDomain: "vue-store-cf980.firebaseapp.com",
      databaseURL: "https://vue-store-cf980.firebaseio.com",
      projectId: "vue-store-cf980",
      storageBucket: "vue-store-cf980.appspot.com",
      messagingSenderId: "708944247528",
      appId: "1:708944247528:web:2300437b93b7e106ee675d",
      measurementId: "G-B78R99D5TZ"
    };
    // Initialize Firebase
    fb.initializeApp(firebaseConfig);
    fb.analytics();
    fb.auth().onAuthStateChanged(user => {
      if (user) {
        this.$store.dispatch("autoLoginUser", user);
      }
    });
  },

  render: h => h(App)
}).$mount("#app");
