<template>
  <v-app>
    <Navbar></Navbar>
    <template v-if="error">
      <v-snackbar
        color="error"
        :multi-line="true"
        :timeout="5000"
        @input="closeError"
        :value="true"
      >
        {{ error }}
        <v-btn dark text @click="closeError">
          Закрыть
        </v-btn>
      </v-snackbar>
    </template>
  </v-app>
</template>

<script>
import Navbar from "./components/Navbar";
export default {
  components: {
    Navbar
  },
  computed: {
    error() {
      return this.$store.getters.error;
    }
  },
  methods: {
    closeError() {
      this.$store.dispatch("clearError");
    }
  }
};
</script>
