<template>
  <v-container>
    <v-layout row>
      <v-flex xs12 sm6 offset-sm3>
        <h1 class="text--secondary mb3">Новый товар</h1>
        <v-form ref="form" v-model="valid" lazy-validation class="mb-3">
          <v-text-field
            v-model="title"
            label="Название товара*"
            name="title"
            type="text"
            :rules="[v => !!v || 'Это поле обязательно']"
          />
          <v-text-field
            label="Производитель товара"
            name="vendor"
            type="text"
            v-model="vendor"
          />
          <v-text-field
            label="Цвет товара"
            name="color"
            type="text"
            v-model="color"
          />
          <v-text-field
            label="Материал товара"
            name="material"
            type="text"
            v-model="material"
          />
          <v-text-field
            label="Цена товара*"
            name="price"
            type="text"
            v-model="price"
            :rules="[v => !!v || 'Это поле обязательно']"
          />
          <v-textarea
            label="Описание товара"
            name="description"
            type="text"
            v-model="description"
            multi-line
          />
        </v-form>
        <v-layout class="mb-3">
          <v-flex xs12>
            <v-btn class="warning">
              Загрузить
              <v-icon right dark>cloud_upload</v-icon>
            </v-btn>
          </v-flex>
        </v-layout>

        <v-layout>
          <v-flex xs12>
            <!-- <img
            src="https://image.ibb.co/fZzq1o/Lenovo_Legion_Y520.jpg"
            height="200px" /> -->
          </v-flex>
        </v-layout>

        <v-layout>
          <v-flex xs12>
            <v-switch
              color="primary"
              v-model="promo"
              label="Добавить в промо-секцию?"
            ></v-switch
          ></v-flex>
        </v-layout>
        <v-layout>
          <v-flex xs12>
            <v-spacer></v-spacer>
            <v-btn
              :disabled="!valid || loading"
              class="success"
              @click="createProduct"
              :loading="loading"
              >Добавить продукт</v-btn
            >
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      valid: false,
      title: "",
      vendor: "",
      color: "",
      material: "",
      price: 0,
      description: "",
      promo: false
    };
  },
  computed: {
    data() {
      return {
        valid: false
      };
    },
    loading() {
      return this.$store.getters.loading;
    }
  },
  methods: {
    createProduct() {
      if (this.$refs.form.validate()) {
        const product = {
          title: this.title,
          vendor: this.vendor,
          color: this.color,
          material: this.material,
          price: this.price,
          description: this.description,
          promo: this.promo,
          imageSrc: "https://image.ibb.co/fZzq1o/Lenovo_Legion_Y520.jpg"
        };
        console.log(product);
        this.$store
          .dispatch("createProduct", product)
          .then(() => {
            this.$router.push("/list");
          })
          .catch(() => {});
      }
    }
  }
};
</script>
