<template>
  <v-container>
    <v-layout row>
      <v-flex xs12 sm6 offset-sm-3>
        <h1 class="text-secondary  mb-3">Заказы</h1>

        <v-list>
          <v-list-item-group>
            <v-list-item v-for="(order, i) in orders" :key="i">
              <template>
                <v-list-item-action>
                  <v-checkbox
                    color="success"
                    :input-value="order.done"
                    @change="markDown(order)"
                  ></v-checkbox>
                </v-list-item-action>

                <v-list-item-content>
                  <v-list-item-title>{{ order.name }}</v-list-item-title>
                  <v-list-item-subtitle>
                    {{ order.phone }}
                  </v-list-item-subtitle>
                </v-list-item-content>

                <v-list-item-action>
                  <v-btn class="primary" :to="'/product/' + order.productId"
                    >Открыть</v-btn
                  >
                </v-list-item-action>
              </template>
            </v-list-item>
          </v-list-item-group>
        </v-list>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      orders: [
        {
          id: "test",
          name: "Pavel",
          phone: "88005553535",
          productId: 1,
          done: false
        }
      ]
    };
  },
  methods: {
    markDown(order) {
      order.done = true;
    }
  }
};
</script>
