<template>
  <v-container fluid fill-height>
    <v-layout align-center justify-center>
      <v-flex xs12 sm8 md6>
        <v-card class="elevation-12">
          <v-toolbar color="primary" dark flat>
            <v-toolbar-title>Форма авторизации</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form ref="form" v-model="valid" lazy-validation>
              <v-text-field
                v-model="email"
                label="E-mail"
                name="email"
                prepend-icon="person"
                type="email"
                :rules="emailRules"
              />
              <v-text-field
                v-model="password"
                label="Пароль"
                name="password"
                :counter="6"
                prepend-icon="lock"
                type="password"
                :rules="passwordRules"
              />
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer />
            <v-btn
              color="primary"
              @click="onSubmit"
              :disabled="!valid || loading"
              :loading="loading"
              >Войти</v-btn
            >
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      email: "",
      password: "",
      valid: false,
      emailRules: [
        v => !!v || "Необходимо ввести E-mail",
        v =>
          /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) ||
          "Введите верный E-mail"
      ],
      passwordRules: [
        v => !!v || "Введите пароль",
        v => (v && v.length >= 6) || "Минимальная длина пароля - 6 символов"
      ]
    };
  },
  computed: {
    loading() {
      return this.$store.getters.loading;
    }
  },
  methods: {
    onSubmit() {
      if (this.$refs.form.validate()) {
        const user = {
          email: this.email,
          password: this.password
        };
        this.$store
          .dispatch("loginUser", user)
          .then(() => {
            this.$router.push("/");
          })
          .catch(() => {});
      }
    },
    created() {
      if (this.$route.query["loginError"]) {
        console.log("Error");
        this.$store.dispatch(
          "setError",
          "Пожалуйста войдите для доступа к данной странице"
        );
      }
    }
  }
};
</script>
